import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { first } from 'rxjs/operators';

@Component({
    selector: 'app-catering',
    templateUrl: './catering.component.html',
    styleUrls: ['./catering.component.scss'],
})
export class CateringComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    public date: string;
    /** Name of the page to display */
    public page: string;
    /** Filter string for searching */
    public search_str: string;
    /** Toggle for new category modal */
    public category: number = 0;
    /** Toggle for new package modal */
    public package: number = 0;
    /** Number of categories being displayed */
    public length: number;
    /** Order type to filter by */
    public order_type: string = '';

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _service: ApplicationService,
        private _org: OrganisationService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._org.initialised.pipe(first(_ => _)).subscribe(() => {
            this.category = 0;
            this._service.title = 'Catering';
            this.subscription(
                'route.params',
                this._route.paramMap.subscribe((params) => {
                    if (params.has('page')) {
                        this.page = params.get('page');
                    }
                })
            );
            this.subscription(
                'route.query_params',
                this._route.queryParamMap.subscribe((params) => {
                    if (params.has('zone') && params.get('zone') !== this._org.building.id) {
                        const id = params.get('zone');
                        const bld = this._org.buildings.find((building) => building.id === id);
                        if (bld) {
                            this._org.building = bld;
                        }
                    }
                })
            );
            this.subscription(
                'building',
                this._org.listen('active_building').subscribe(() => {
                    this._router.navigate([], {
                        relativeTo: this._route,
                        queryParams: { zone: this._org.building.id },
                    });
                })
            );
        });
    }
}
