import { MockHttpRequestHandler } from '@placeos/ts-client';

import { API } from './common.mock';
import { MOCK_BOOKINGS } from './bookings.mock';
import { timePeriodsIntersect } from 'src/app/services/data/bookings/booking.utilities';

import * as dayjs from 'dayjs';

// Handler for bookings index
window.control.handlers.push({
    path: `${API}/reports/bookings`,
    metadata: [],
    method: 'GET',
    callback: (event) => {
        const start = dayjs(+event.query_params.start_date * 1000).startOf('d');
        const end = dayjs(+event.query_params.end_date * 1000).endOf('d');
        const bookings = MOCK_BOOKINGS.filter((bkn: any) => {
            return timePeriodsIntersect(
                bkn.start_epoch * 1000 || bkn.date,
                bkn.end_epoch * 1000 || bkn.date,
                start.valueOf(),
                end.valueOf()
            );
        });
        console.log('Bookings:', start.format('DD MMM YYYY'), end.format('DD MMM YYYY'), bookings);
        return bookings.map(bkn => ({ ...bkn, start: bkn.start_epoch * 1000, end: bkn.end_epoch * 1000 }));
    },
} as MockHttpRequestHandler);

// Handler for bookings index
window.control.handlers.push({
    path: `${API}/reports/catering`,
    metadata: [],
    method: 'GET',
    callback: (event) => {
        const start = dayjs(+event.query_params.start_date * 1000).startOf('d');
        const end = dayjs(+event.query_params.end_date * 1000).endOf('d');
        console.log('Event:', event);
        const bookings = MOCK_BOOKINGS.filter((bkn: any) => {
            return timePeriodsIntersect(
                bkn.start_epoch * 1000 || bkn.date,
                bkn.end_epoch * 1000 || bkn.date,
                start.valueOf(),
                end.valueOf()
            );
        });
        console.log('Bookings:', start.format('DD MMM YYYY'), end.format('DD MMM YYYY'), bookings);
        let catering = [];
        for (const booking of bookings) {
            catering = catering.concat([...booking.catering]);
        }
        return catering.map((order) => ({
            delivery_time: dayjs(order.booking_date)
                .add(order.delivery_time, 'm')
                .format('DD MMM YYYY, h:mm A'),
            location_id: order.location_id,
            items: order.items.map((i) => i.name),
        }));
    },
} as MockHttpRequestHandler);
