import { Component, Input, SimpleChanges, OnChanges } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ANIMATION_SHOW_CONTRACT_EXPAND } from 'src/app/shared/globals/angular-animations';
import {
    VisitorsFormModalComponent,
    VisitorsFormModalData,
} from '../../overlays/visitors-form-modal/visitors-form-modal.component';
import { unique } from 'src/app/shared/utilities/general.utilities';
import { BookingsService } from 'src/app/services/data/bookings/bookings.service';
import { ApplicationService } from 'src/app/services/app.service';

@Component({
    selector: 'a-visitor-booking',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
    animations: [ANIMATION_SHOW_CONTRACT_EXPAND],
})
export class VisitorBookingItemComponent implements OnChanges {
    /** Booking details to display */
    @Input() public event: Booking;
    /** Search filter string */
    @Input() public search: string;
    /** Whether attendee list should be shown */
    public show_attendees: boolean;
    /** Whether all the attendees have checked in */
    public checked_in: boolean = false;
    /** Whether all attendees are being checked in */
    public loading: boolean;

    /** Whether event will start soon or has started */
    public get upcoming() {
        return this.event.status === 'upcoming' || this.event.status === 'in_progress';
    }

    /** Display string for checkin statuses of all attendees */
    public get status(): string {
        const { checked_in, count } = this.checkedInStatus();
        return `${checked_in} / ${count}`;
    }

    constructor(private _service: ApplicationService, private _bookings: BookingsService, private _dialog: MatDialog) {}

    public checkedInStatus() {
        const checked_in_users = this.event.checked_in;
        return this.event.attendees.reduce(
            (counts, user) => {
                /* istanbul ignore else */
                if (user.external) {
                    counts.count++;
                    /* istanbul ignore else */
                    if (checked_in_users.indexOf(user.email) >= 0) {
                        counts.checked_in++;
                    }
                }
                return counts;
            },
            { checked_in: 0, count: 0 }
        );
    }

    /**
     * Checkin the attendee
     */
    public async checkin() {
        this.loading = true;
        const value = await this._bookings
            .checkin(this.event.id, {
                host_email: this.event.organiser.email,
                attendees: unique(this.event.attendees.map(user => ({ name: user.name, email: user.email }))),
                booking_id: this.event.id,
                icaluid: this.event.icaluid,
                id: this.event.id,
                notify_users: [...this.event.notify_users],
                response_type: 'accepted',
                room_id: this.event.space.id,
            })
            .catch((err) => {
                this._service.notifyError(`Error checking in all attendees for booking. Error: ${err.message || err}`);
            });
        if (value) {
            this.event = new Booking({
                ...this.event.toJSON(),
                checked_in: unique([...this.event.checked_in, ...this.event.attendees.map(user => user.email)]),
            });
        }
        this.loading = false;
    }

    /** URL for mailing all attendees of the event */
    public get mail_url(): string {
        const subject = encodeURIComponent(
            `${this.event.title}" at ${this.event.start_time}, ${this.event.date_string}`
        );
        return `mailto:${this.event.attendees.filter(user => !user.email.includes('@guest'))
            .map((user) => user.email)
            .join(',')}?Subject=${subject}"`;
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.search) {
            this.show_attendees = !!this.search;
        }
        if (changes.event) {
            const { checked_in, count } = this.checkedInStatus();
            this.checked_in = checked_in >= count;
        }
    }

    public openEditModal() {
        this._dialog.open<VisitorsFormModalComponent, VisitorsFormModalData>(
            VisitorsFormModalComponent,
            {
                data: {
                    booking: this.event,
                },
            }
        );
    }
}
