import { CateringItem } from "./catering-item.class";
import { HashMap } from "../../../shared/utilities/types.utilities";

export class CateringCategory extends CateringItem {

    constructor(data: HashMap) {
        super(data);
        (this as any).must_select = this.must_select || 0;
        (this as any).order_anytime = !!data.order_anytime && this.package;
    }

    /**
     * Convert class object into plain object
     */
    public toJSON(this: CateringItem): HashMap {
        const obj = super.toJSON();
        obj.order_anytime = !!obj.order_anytime && obj.package;
        return obj;
    }
}
