import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { Observable, Subject, of } from 'rxjs';
import {
    debounceTime,
    distinctUntilChanged,
    switchMap,
    catchError,
    map,
    first,
} from 'rxjs/operators';

import { Space } from 'src/app/services/data/spaces/space.class';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { BookingsService } from 'src/app/services/data/bookings/bookings.service';
import {
    replaceBookings,
    timePeriodsIntersect,
} from 'src/app/services/data/bookings/booking.utilities';

import * as dayjs from 'dayjs';

@Component({
    selector: 'visitor-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
})
export class VisitorTimelineComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    @Input() public date: number;
    /** Filter string */
    @Input() public search: string;
    /** Whether all bookings should be shown */
    @Input() public show_all: boolean;
    /** List of visitor bookings for the current date */
    public bookings: Booking[] = [];
    /** List of filtered bookings */
    public filtered_bookings: Booking[] = [];
    /** Whether visitor list is loading */
    public loading: boolean;
    /** List of items from an API search */
    public search_results$: Observable<Booking[]>;
    /** Subject holding the value of the search */
    public readonly search$ = new Subject<string>();

    /** Display string for the selected date */
    public get date_display() {
        const date = dayjs(this.date);
        return date.isSame(dayjs(), 'd') ? 'Today' : date.format('DD MMM YYYY');
    }

    public get active_index(): number {
        /* istanbul ignore else */
        if (this.date_display === 'Today') {
            const date = dayjs().startOf('m').valueOf();
            return this.filtered_bookings.findIndex((bkn) => date > bkn.date);
        }
        return -1;
    }

    constructor(
        private _org: OrganisationService,
        private _spaces: SpacesService,
        private _bookings: BookingsService
    ) {
        super();
    }

    ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this.initBookings();
            this.subscription(
                'building',
                this._org
                    .listen('active_building')
                    .subscribe((_) => this.search$.next(`${this.date}|${_.id}`))
            );
            this.interval(
                'update_bookings',
                () => this.search$.next(`${this.date}|${dayjs().unix()}`),
                30 * 1000
            );
            this.subscription(
                'bookings',
                this._bookings.booking_list.subscribe(() => this.updateEvents())
            );
            this.updateEvents();
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.date) {
            this.search$.next(`${this.date}`);
        }
        /* istanbul ignore else */
        if (changes.search || changes.show_all) {
            this.filtered_bookings = this.filter(this.search);
        }
    }

    /* istanbul ignore next */
    public trackByFn(index: number, booking: Booking) {
        return booking.id;
    }

    /** Filter the visitor bookings */
    public filter(str: string = '') {
        const search = str.toLowerCase();
        return this.bookings
            .filter((booking) => {
                return booking.attendees.find(
                    (user) =>
                        user.name.toLowerCase().indexOf(search) >= 0 ||
                        user.email.toLowerCase().indexOf(search) >= 0
                );
            })
            .filter((bkn) => (this.show_all ? true : bkn.has_visitors));
    }

    private updateEvents() {
        const date = dayjs(this.date).startOf('d');
        const bookings = this._bookings.booking_list.getValue().filter((booking) => {
            const start = dayjs(booking.date);
            const end = start.add(booking.duration, 'm');
            return timePeriodsIntersect(
                date.valueOf(),
                date.endOf('d').valueOf(),
                start.valueOf(),
                end.valueOf()
            );
        })
        .filter((bkn) =>
            !bkn.declined && bkn.space_list.find((space) => space.zones.includes(this._org.building.id))
        );
        this.bookings = bookings.map((bkn) => {
            const data = bkn.toJSON();
            const space =
                bkn.space_list.find((space) => space.zones.includes(this._org.building.id)) ||
                bkn.space;
            return new Booking({
                ...data,
                room_ids: [space.email].concat(bkn.space_list.map((space) => space.email)),
            });
        });
        this.filtered_bookings = this.filter(this.search);
    }

    private initBookings() {
        // Listen for input changes
        this.search_results$ = this.search$.pipe(
            debounceTime(100),
            distinctUntilChanged(),
            switchMap((_) => {
                this.loading = true;
                this.updateEvents();
                const date = dayjs(this.date).startOf('d');
                const zone = this._org.building.id;
                return this._spaces.query({
                    zone_ids: zone,
                    available_from: date.unix(),
                    available_to: date.endOf('d').unix(),
                });
            }),
            catchError((_) => of([])),
            map((list: Space[]) => {
                const date = dayjs(this.date).startOf('d');
                let bookings = this._bookings.booking_list.getValue();
                list.forEach(
                    (space) =>
                        (bookings = replaceBookings(
                            bookings,
                            space.bookings.map((bkn) => new Booking(bkn)),
                            {
                                space: space.email,
                                from: date.valueOf(),
                                to: date.endOf('d').valueOf(),
                            }
                        ))
                );
                bookings.sort((a, b) => a.date - b.date);
                this._bookings.booking_list.next(bookings);
                this.updateEvents();
                return bookings;
            })
        );
        // Process API results
        this.subscription(
            'search_results',
            this.search_results$.subscribe((_) => (this.loading = false))
        );
    }
}
