import { HashMap } from '../../../shared/utilities/types.utilities';
import { ISpaceAvailabilityOptions, Space } from './space.class';

import * as dayjs from 'dayjs';
import { predictableRandomInt } from 'src/app/shared/utilities/general.utilities';
import { Building } from '../organisation/building.class';

export function availabilityOptionsToQuery(options: ISpaceAvailabilityOptions): HashMap {
    let query: HashMap = {};
    if (options) {
        query = { ...options };
        if (options.date) {
            const date = dayjs(options.date).startOf('m');
            query.available_from = date.unix();
            query.available_to = date.add(options.duration || 60, 'm').unix();
            delete query.date;
            delete query.duration;
        }
    }
    return query;
}

/**
 * Compare two spaces to determine order
 * @param first First space to compare
 * @param second Second space to compare
 */
export function sort(first: Space, second: Space, blds: Building[] = []) {
    const bld = blds.find(bld => first.zones.includes(bld.id));
    const bld_b = blds.find(bld => second.zones.includes(bld.id));
    if (bld) {
        if (bld !== bld_b) {
            return (bld.name).localeCompare(bld_b?.name);
        }
        const sort_order = [...bld.sort_order].reverse();
        for (const zone_id of sort_order) {
            if (zone_id === '*') { continue; }
            const a_has_zone = first.zones.indexOf(zone_id) >= 0;
            const b_has_zone = second.zones.indexOf(zone_id) >= 0;
            if (a_has_zone && !b_has_zone) {
                return 1;
            } else if (b_has_zone && !a_has_zone) {
                return -1;
            }
        }
    }
    return first.name.localeCompare(second.name);
}
