/**
 * Generate a mocked version of the application service
 */
export function generateMockAppService() {
    SERVICE = {
        setting: jest.fn(),
        notify: jest.fn(),
        notifyInfo: jest.fn(),
        notifyWarn: jest.fn(),
        notifySuccess: jest.fn(),
        notifyError: jest.fn(),
        log: jest.fn(),
        navigate: jest.fn(),
        navigateBack: jest.fn(),
        get: jest.fn(),
        listen: jest.fn(),
        set: jest.fn(),
        Composer: { auth: { token: 'test' } },
        Hotkeys: { listen: jest.fn() },
        Users: generateMockDataService('UsersService'),
        Organisation: {
            levelWithID: jest.fn(),
            listen: jest.fn(),
            initialised: of(true),
            building: new Building(generateMockBuilding({ id: 'bld-01' }))
        },
        Locations: generateMockDataService('LocationsService'),
        Base: generateMockDataService('BaseService'),
        Menu: generateMockDataService('CateringMenuService'),
        Spaces: generateMockDataService('SpacesService'),
        Bookings: generateMockDataService('BookingsService'),
        Reports: generateMockDataService('ReportsService'),
        CateringItems: generateMockDataService('CateringItemsService'),
        CateringCategories: generateMockDataService('CateringCategoriesService'),
        initialised: of(true)
    };
    ServiceManager.setService(BaseDataClass, SERVICE.Base);
    ServiceManager.setService(Organisation, SERVICE.Organisation);
    ServiceManager.setService(Building, SERVICE.Organisation);
    ServiceManager.setService(User, SERVICE.Users);
    ServiceManager.setService(Booking, SERVICE.Bookings);
    ServiceManager.setService(Space, SERVICE.Spaces);
    ServiceManager.setService(MapLocation, SERVICE.Locations);
    ServiceManager.setService(Report, SERVICE.Reports);
    ServiceManager.setService(CateringItem, SERVICE.CateringItems);
    ServiceManager.setService(CateringCategory, SERVICE.CateringCategories);
    SERVICE.Organisation.buildings = [SERVICE.Organisation.building];
    SERVICE.Users.current = new User(generateMockUser());
    SERVICE.Users.initialised = of(true);
    SERVICE.Bookings.booking_list = new BehaviorSubject(new Array(10).fill(0).map(_ => new Booking(generateMockBooking())));
    SERVICE.listen.mockReturnValue(of(null, []));
    return SERVICE;
}

export function generateMockDataService(name: string) {
    const service = {
        find: jest.fn(),
        filter: jest.fn(),
        listen: jest.fn(),
        get: jest.fn(),
        set: jest.fn(),
        setting: jest.fn(),
        query: jest.fn(),
        show: jest.fn(),
        add: jest.fn(),
        save: jest.fn(),
        remove: jest.fn(),
        delete: jest.fn(),
        update: jest.fn(),
        addFrom: jest.fn(),
        removeFrom: jest.fn(),
        task: jest.fn(),
        available: jest.fn(),
        process: jest.fn(),
        updateList: jest.fn(),
        clearList: jest.fn(),
        removeFromList: jest.fn(),
        accept: jest.fn(),
        decline: jest.fn(),
        checkin: jest.fn(),
        is_logged_in: false,
        initialised: of(true),
        name
    };
    service.save.mockImplementation(_ => Promise.resolve(new Booking(_)));
    service.listen.mockReturnValue(of(null));
    service.filter.mockReturnValue([]);
    return service;
}

let SPACE_COUNT: number = 0;

export function generateMockSpace(overrides: HashMap = {}): HashMap {
    const id = `space-${SPACE_COUNT++}`;
    const name = `${faker.name.firstName()} ${faker.name.lastName()} Space`;
    const linked = Math.floor(predictableRandomInt(99999)) % 2 === 0 && SPACE_COUNT > 1;
    return {
        id,
        name,
        long_name: `${name} with an long name`,
        map_id: `${SPACE_COUNT}`,
        capacity: Math.floor(predictableRandomInt(20) + 1) * 2,
        email: `${name.toLowerCase().split(' ').join('.')}@${USER_DOMAIN}`,
        type: faker.commerce.productName(),
        searchable: Math.floor(predictableRandomInt(99999)) % 2 === 0,
        controlable: Math.floor(predictableRandomInt(99999)) % 2 === 0,
        bookable: Math.floor(predictableRandomInt(99999)) % 2 === 0,
        cost_hour: Math.floor(predictableRandomInt(300)) * 100,
        setup: Math.floor(predictableRandomInt(6)) * 5,
        breakdown: Math.floor(predictableRandomInt(6)) * 5,
        zones: ['zone_lvl-0'],
        support_url: `/control/#/${id}`,
        bookings: Array(10).fill(0).map(i => generateMockBooking()),
        linked_rooms: linked ? [`space-${Math.floor(predictableRandomInt(SPACE_COUNT - 1))}`] : [],
        image: faker.image.business(),
        ...overrides
    };
}

let BOOKING_COUNT = 0;
let BOOKING_DATE = dayjs().hour(6).minute(0).subtract(10, 'd').startOf('m');

/**
 * Set the initial time used for generating mock bookings
 * @param time New initial time as ms from UTC epoch
 */
export function setMockBookingStartDatetime(time: number) {
    BOOKING_DATE = dayjs(time).startOf('m');
}

/**
 * Create mock raw API data for a booking
 * @param override Overrides the properties of the generated booking with it's own
 */
export function generateMockBooking(override: HashMap = {}) {
    const id = `booking-${BOOKING_COUNT++}`;
    BOOKING_DATE = BOOKING_DATE.add(Math.floor(predictableRandomInt(4) + 2) * 15, 'm');
    const start = BOOKING_DATE.valueOf();
    const duration = Math.floor(predictableRandomInt(4) + 2) * 15;
    BOOKING_DATE = BOOKING_DATE.add(Math.floor(predictableRandomInt(4)) * 15, 'm');
    const organizer = generateMockUser({ external: false, visitor: false });
    const orders = Array(Math.floor(predictableRandomInt(5)))
    .fill(0)
    .map(_ => generateMockCateringOrder({ booking_date: start }, override.room_ids, override.menu))
    return {
        id,
        icaluid: padZero(Math.floor(predictableRandomInt(99999999)), 8),
        title: `${faker.commerce.productName()} Meeting`,
        attendees: Array(Math.floor(predictableRandomInt(5) + 2))
            .fill(0)
            .map((_, i) => i === 0 ? organizer : generateMockUser(override.users)),
        organizer,
        start_epoch: dayjs(start).unix(),
        end_epoch: dayjs(start).add(duration, 'm').unix(),
        description: faker.lorem.paragraph(),
        notes: [{ type: 'other', message: faker.lorem.paragraph() }],
        location: faker.address.city(),
        has_catering: Math.floor(predictableRandomInt(34567)) % 3 === 0,
        booking_type: ['internal', 'training', 'setup', 'client', 'Interview'][predictableRandomInt(5)],
        setup: { 'space-01': Math.max(0, (predictableRandomInt(12) - 6) * 5) },
        breakdown: { 'space-01': Math.max(0, (predictableRandomInt(12) - 6) * 5) },
        status: {},
        catering: orders,
        room_ids: [],
        ...override,
    };
}

let BLD_COUNT = 0;
let LVL_COUNT = 0;

/**
 * Generate raw mock data for creating a building
 * @param id Forced ID for the mock
 */
export function generateMockBuilding(overrides: HashMap = {}): HashMap {
    const id = `zone_bld-${BLD_COUNT++}`;
    const levels = Array(10)
        .fill(0)
        .map(i => generateMockLevel());
    const features: any = {};
    for (const lvl of levels) {
        const count = Math.floor(predictableRandomInt(3) + 2);
        features[lvl.level_id] = {};
        for (let i = 0; i < count; i++) {
            features[lvl.level_id][faker.commerce.productName()] = `feature-${i + 1}`;
        }
    }
    return {
        id,
        zone_id: id,
        extras: Array(10)
            .fill(0)
            .map(i => {
                const name = faker.commerce.productName();
                return {
                    extra_id: name
                        .split(' ')
                        .join('-')
                        .toLowerCase(),
                    extra_name: name
                };
            }),
        loan_items: Array(10)
            .fill(0)
            .map(i => {
                const name = faker.commerce.productName();
                return {
                    extra_id: name
                        .split(' ')
                        .join('-')
                        .toLowerCase(),
                    extra_name: name
                };
            }),
        levels,
        roles: {
            'first-aiders': Array(10)
                .fill(0)
                .map(i => generateMockUser())
        },
        neighbourhoods: features,
        settings: {
            test: {
                nested: {
                    level2: true,
                    org: false
                }
            }
        },
        ...overrides
    };
}

/**
 * Generate raw mock data for a building level
 * @param id Forced ID for the mock
 * @param map_url Map URL for the level
 */
export function generateMockLevel(id?: string, map_url?: string): HashMap {
    if (!id) {
        id = `zone_lvl-${LVL_COUNT++}`;
    }
    return {
        level_id: id,
        level_name: `Level ${LVL_COUNT}`,
        map_url
    };
}

let USER_COUNT = 0;

export let USER_DOMAIN = 'place.tech';

const USER_EMAILS: string[] = [];

/**
 * Generate raw mock data for a user
 * @param id Forced ID for the mock
 * @param name Forced name for the user
 * @param external Whether user is external of the organisation
 */
export function generateMockUser(override: HashMap = {}): HashMap {
    const id = `user-${USER_COUNT++}`;
    const name = `${faker.name.firstName()} ${faker.name.lastName()}`;
    const external = override.external || !(predictableRandomInt(99999) % 2);
    const organisation = external ? faker.company.companyName() : USER_DOMAIN.split('.')[0];
    let delegates: string[] = [];
    const delegate_count = Math.min(predictableRandomInt(4) + 1, USER_EMAILS.length);
    for (let i = 0; i < delegate_count; i++) {
        delegates.push(USER_EMAILS[Math.floor(predictableRandomInt(USER_EMAILS.length))]);
    }
    delegates = unique(delegates);
    const email = `${name
        .split(' ')
        .join('.')
        .toLowerCase()}@${external ? 'not-' : ''}${USER_DOMAIN}`;
    USER_EMAILS.push(email);
    return {
        id,
        name,
        first_name: name.split(' ')[0],
        last_name: name.split(' ')[1],
        email,
        phone: faker.phone.phoneNumber(),
        visitor: external,
        organisation: {
            id: organisation
                .split(' ')
                .join('.')
                .toLowerCase(),
            name: organisation
        },
        department: faker.commerce.department(),
        staff_code: padZero(Math.floor(predictableRandomInt(99999)), 5),
        delegates,
        image: faker.image.avatar(),
        ...override
    };
}

export function generateMockCateringOrder(overrides: HashMap = {}, room_ids: string[] = [], menu: HashMap<HashMap[]> = {}) {
    return {
        id: `order-${predictableRandomInt(9999_9999)}`,
        delivery_time: predictableRandomInt(6) * 5,
        location_id: room_ids[predictableRandomInt(room_ids.length)],
        items: new Array(predictableRandomInt(5, 1)).fill(0).map(i => pickMenuItem(menu)),
        ...overrides
    }
}

function pickMenuItem(menu_map: HashMap<HashMap[]> = {}) {
    const zones = Object.keys(menu_map);
    const menu = menu_map[zones[predictableRandomInt(zones.length)]] || [];
    const category = menu[predictableRandomInt(menu.length)] || { items: [] };
    const group = { ...(category.items[predictableRandomInt(category.items.length)] || {}) };
    if (!group.package && group.items?.length) {
        const item = { ...group.items[predictableRandomInt(group.items.length)] };
        item.amount = predictableRandomInt(5, 1);
        return item;
    }
    group.amount = predictableRandomInt(5, 1);
    return group;
}

export function generateMockLocation(overrides: HashMap, fixed_locations: string[], maps: string[]): HashMap {
    const fixed = predictableRandomInt(999_999_999) % 2 === 0;
    if (fixed && (!overrides || overrides.fixed)) {
        return {
            map_id: fixed_locations[predictableRandomInt(fixed_locations.length)],
            level: maps[predictableRandomInt(maps.length)],
            ...overrides
        }
    } else {
        return {
            x: predictableRandomInt(900, 100),
            x_max: 1000,
            y: predictableRandomInt(500, 100),
            level: maps[predictableRandomInt(maps.length)],
            confidence: predictableRandomInt(30),
            ...overrides
        }
    }
}

let ORG_COUNT = 0;

export function generateMockOrganisation(): HashMap {
    const buildings = Array(3).fill(0).map(i => generateMockBuilding());
    return {
        id: `zone_org-${ORG_COUNT++}`,
        name: `Organisation ${ORG_COUNT}`,
        buildings,
        settings: {
            discovery_info: {
                buildings
            },
            test: {
                nested: {
                    org: true
                }
            }
        }
    };
}


