import { Component, OnInit, Input, SimpleChanges, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { FormGroup, FormControl } from '@angular/forms';
import { toDate } from 'date-fns-tz';

import { CateringCategory } from 'src/app/services/data/catering/catering-category.class';
import { Space } from 'src/app/services/data/spaces/space.class';
import { Building } from 'src/app/services/data/organisation/building.class';
import { Identity, DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { CateringItem } from 'src/app/services/data/catering/catering-item.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { CateringOrder } from 'src/app/services/data/catering/catering-order.class';
import {
    BookingCateringConfirmModalComponent,
    CateringConfirmModalData,
} from 'src/app/overlays/catering-confirm-modal/catering-confirm-modal.component';

import * as dayjs from 'dayjs';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { CateringMenuService } from 'src/app/services/data/catering/catering-menu.service';
import { padZero } from 'src/app/shared/utilities/general.utilities';
import { BehaviorSubject } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';

@Component({
    selector: 'a-catering-order-details',
    templateUrl: './order-details.component.html',
    styleUrls: ['./order-details.component.scss'],
})
export class BookingCateringOrderDetailsComponent extends BaseDirective implements OnInit {
    /** Catering order to edit details of */
    @Input() public order: CateringOrder;
    /** Booking date */
    @Input() public date: number;
    /** Booking duration */
    @Input() public duration: number;
    /** Booking duration */
    @Input() public space_list: Space[];
    /** Whether booking is all day */
    @Input() public all_day: boolean;
    /** Whether top header details should be shown */
    @Input() public hide_details: boolean;
    /** Whether to show smaller version of component */
    @Input() public compact: boolean;
    /** Emitter for changes to the catering order */
    @Output() public event = new EventEmitter<CateringOrder>();
    /** Form controls for order */
    public form: FormGroup;
    /** List of available catering item categories */
    public category_list: CateringCategory[] = [];
    /** Active category to display details for */
    public active_category: CateringCategory;
    /** List of available times for delivering catering */
    public available_times: Identity[] = [];
    /** Whether the menu is loading */
    public loading: boolean;
    /** Whether the search input should be shown */
    public show_search: boolean;
    /** Current search string for filtering */
    public readonly search$ = new BehaviorSubject('');
    /** List of menu items that match the search string */
    public matched_items: CateringItem[] = [];

    @ViewChild('input') private _input_el: ElementRef<HTMLInputElement>;

    /** Whether user is able to order from the currently selected space */
    public get can_order(): boolean {
        return (
            (this.active_building.has_catering || this.category_list.length) &&
            (!this.within_restricted_time || this.has_available_items) &&
            !this.out_of_hours
        );
    }

    /** Building associated with the selected space */
    public get active_building(): Building {
        const form_controls = (this.form || { controls: { location_id: { value: '' } } }).controls;
        const space: Space =
            this.space_list.find((space) => space.email == form_controls.location_id.value) ||
            ({ level: {} } as any);
        return (
            this._org.buildings.find((bld) => space.level.building_id === bld.id) ||
            ({ catering_restricted_from: 0 } as any)
        );
    }

    /** Currency code for the active building */
    public get symbol(): string {
        return this.active_building.currency || 'USD';
    }

    /** Number of items in the order */
    public get item_count(): number {
        return this.form
            ? this.form.controls.items.value.reduce((total, item) => total + item.amount, 0)
            : 0;
    }

    /* istanbul ignore next */
    /** Whether booking time is outside of catering hours */
    public get out_of_hours(): boolean {
        return this.form.controls.delivery_time.value < 0;
    }

    /** Whether booking is with the restricted ordering period */
    public get within_restricted_time(): boolean {
        return false;
    }

    /** Whether items are available for order at anytime */
    public get has_available_items(): boolean {
        return this.category_list.reduce(
            (available, category) => available || category.can_order_anytime,
            false
        );
    }

    constructor(
        private _menu: CateringMenuService,
        private _org: OrganisationService,
        private _dialog: MatDialog
    ) {
        super();
    }

    public ngOnInit(): void {
        this.generateAvailableTimes();
        this.subscription('search', this.search$.pipe(map((str) => {
            const search = str.toLowerCase();
            const items = [];
            for (const category of this.category_list) {
                if (category.items) {
                    for (const group of category.items) {
                        if ((group as any).items) {
                            if ((group as any).package && (group as any).items.find(item => item.name.toLowerCase().includes(search))) {
                                items.push(group);
                            } else {
                                for (const item of (group as any).items) {
                                    if (item.name.toLowerCase().includes(search)) {
                                        items.push(item);
                                    }
                                }
                            }
                        } else {
                            if (group.name.toLowerCase().includes(search)) {
                                items.push(group);
                            }
                        }
                    }
                }
            }
            return items;
        })).subscribe((items) => this.matched_items = items))
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.date || changes.duration) {
            this.generateAvailableTimes();
            /* istanbul ignore else */
            if (
                this.form &&
                !this.available_times.find(
                    (time) => time.id === this.form.controls.delivery_time.value
                )
            ) {
                this.form.controls.delivery_time.setValue(this.available_times[0].id);
            }
        }
        /* istanbul ignore else */
        if (changes.space_list) {
            /* istanbul ignore else */
            if (this.form && this.space_list && !this.form.controls.location_id.value) {
                this.form.controls.location_id.setValue(this.space_list[0].email);
            }
        }
        /* istanbul ignore else */
        if (changes.order && this.order) {
            this.initForm();
        }
    }

    public focusSearch() {
        if (this._input_el && this.show_search) {
            this._input_el.nativeElement.focus();
        }
    }

    public initForm() {
        this.form = new FormGroup({
            id: new FormControl(this.order.id),
            booking_date: new FormControl(
                this.all_day ? dayjs(this.date).startOf('d').valueOf() : this.date
            ),
            location_id: new FormControl(this.order.location_id || this.space_list[0].email),
            delivery_time: new FormControl(this.order.delivery_time || this.available_times[0].id),
            items: new FormControl(this.order.items.map((item) => new CateringItem(item))),
        });
        this.subscription(
            'space_change',
            this.form.controls.location_id.valueChanges.subscribe((space) => {
                this.generateAvailableTimes();
                if (
                    !this.available_times.find(
                        (time) => time.id === this.form.controls.delivery_time.value
                    )
                ) {
                    this.form.controls.delivery_time.setValue(this.available_times[0].id);
                }
                this.form.controls.items.setValue([]);
                this.loadMenu();
            })
        );
        this.loadMenu();
        this.generateAvailableTimes();
        if (
            this.form &&
            !this.available_times.find((time) => time.id === this.form.controls.delivery_time.value)
        ) {
            this.form.controls.delivery_time.setValue(this.available_times[0].id);
        }
    }

    public confirmOrder() {
        const ref = this._dialog.open<
            BookingCateringConfirmModalComponent,
            CateringConfirmModalData
        >(BookingCateringConfirmModalComponent, {
            maxWidth: '95vw',
            data: {
                all_day: this.all_day,
                date: this.date,
                form: this.form,
            },
        });
        this.subscription(
            'confirm_event',
            ref.componentInstance.event.subscribe((event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    const order = new CateringOrder({ ...this.order, ...this.form.value });
                    this.event.emit(order);
                    ref.close();
                }
            })
        );
        ref.afterClosed().subscribe(() => this.unsub('confirm_event'));
    }

    private loadMenu() {
        const space = this.space_list.find(
            (space) => space.email === this.form.controls.location_id.value
        );
        this.loading = true;
        this._menu
            .query({ zone_id: this._org.buildings.find((bld) => space.zones.includes(bld.id)).id })
            .then(
                (list) => {
                    this.loading = false;
                    this.category_list = list.map((i) => new CateringCategory(i));
                },
                () => (this.loading = false)
            );
    }

    /** Generate a list of available delivery times for the given booking time */
    private generateAvailableTimes() {
        const start_date = this.all_day ? dayjs(this.date).startOf('d') : dayjs(this.date);
        let { start, end } = this.generateStartAndEndTimes();
        start = start.minute(Math.ceil(start.minute() / 5) * 5);
        this.available_times = [];
        while (start.isBefore(end, 'm')) {
            this.available_times.push({
                id: Math.abs(start_date.diff(start, 'm')),
                name: start.format('h:mm A'),
            });
            start = start.add(5, 'm');
        }
        /* istanbul ignore else */
        if (this.available_times.length === 0) {
            this.available_times.push({ id: -1, name: 'Out of hours' });
        }
    }

    private generateStartAndEndTimes(): { start: dayjs.Dayjs; end: dayjs.Dayjs } {
        let start = dayjs(this.date);
        const now = dayjs();
        /* istanbul ignore else */
        if (this.all_day) {
            start = start.isSame(now, 'd') ? now : start.startOf('d');
        }
        let end = this.all_day ? start.endOf('d') : start.add(this.duration, 'm');
        let building_time = dayjs();
        const space_email = this.form ? this.form.controls.location_id.value : null;
        let catering_hours = { start: 7, end: 20 };
        building_time = building_time.minute(0).hour(catering_hours.start);
        if (space_email) {
            const space = this.space_list.find((space) => space.email === space_email);
            const building = this._org.buildings.find((bld) => space?.zones.includes(bld.id));
            catering_hours = building?.catering_hours || catering_hours;
            building_time = building_time.minute(0).hour(catering_hours.start);
            if (building && building.timezone) {
                const hour = padZero(Math.floor(catering_hours.start), 2);
                const minute = padZero(Math.floor(catering_hours.start * 60) % 60, 2);
                building_time = dayjs(
                    toDate(`${start.format(`YYYY-MM-DD`)}T${hour}:${minute} ${building.timezone}`)
                );
            }
        }
        const as_dayjs = dayjs(building_time);
        if (this.all_day || this.duration >= (catering_hours.end - catering_hours.start) * 60) {
            if (start.isBefore(as_dayjs, 'm')) {
                start = as_dayjs;
                end = start.add(catering_hours.end - catering_hours.start, 'h');
            }
        }
        if (start.isBefore(as_dayjs, 'm')) {
            start = as_dayjs;
        }
        const possible_end = building_time.add(catering_hours.end - catering_hours.start, 'h');
        if (end.isAfter(possible_end, 'm')) {
            end = possible_end;
        }
        return { start, end };
    }
}
