import {
    Component,
    OnInit,
    Input,
    SimpleChanges,
    EventEmitter,
    Output,
    OnChanges,
} from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import {
    debounceTime,
    distinctUntilChanged,
    switchMap,
    catchError,
    map,
    first,
} from 'rxjs/operators';
import { Observable, of, Subject } from 'rxjs';

import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { Space, ISpaceAvailabilityOptions } from 'src/app/services/data/spaces/space.class';
import { BookingFlowEvent } from '../booking-modal.component';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { randomInt } from 'src/app/shared/utilities/general.utilities';

import * as dayjs from 'dayjs';

@Component({
    selector: 'booking-find-space',
    templateUrl: './find-space.component.html',
    styleUrls: ['./find-space.component.scss'],
})
export class BookingFindSpaceComponent extends BaseDirective implements OnInit, OnChanges {
    /** Form control for setting the active space */
    @Input() public spaces: FormControl;
    /** Form group for booking */
    @Input() public form: FormGroup;
    /** Emitter for flow update events */
    @Output() public event = new EventEmitter<BookingFlowEvent>();

    /** List of spaces available during the required period */
    public space_list: Space[] = [];
    /** List of users from an API search */
    public search_results$: Observable<Space[]>;
    /** Whether space list is loading */
    public loading: boolean;
    /** List of zones to filter the spaces on */
    public zone_ids: string[] = [];
    /** Subject holding the value of the search */
    public readonly change$ = new Subject<string>();

    /**  Whether multiple spaces are allowed to be selected */
    public get multiple(): boolean {
        return this._service.setting('app.booking.multiple_spaces');
    }

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _org: OrganisationService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            let request_id = 0;
            // Listen for input changes
            this.search_results$ = this.change$.pipe(
                debounceTime(400),
                distinctUntilChanged(),
                switchMap((_) => {
                    this.loading = true;
                    request_id = randomInt(9999_9999);
                    const date = dayjs(this.form.controls.date.value);
                    const query: ISpaceAvailabilityOptions = {
                        date: this.form.controls.all_day.value
                            ? date.startOf('d').valueOf()
                            : date.valueOf(),
                        duration: this.form.controls.all_day.value
                            ? 24 * 60
                            : this.form.controls.duration.value,
                        zone_ids: this._org.building.id,
                        bookable: true
                    };
                    /* istanbul ignore else */
                    if (this.zone_ids && this.zone_ids.length) {
                        query.zone_ids = this.zone_ids.join(',');
                    }
                    const id = request_id;
                    return this._spaces.available(query).then(async (list) => ({ id, list }));
                }),
                catchError((_) => of({ id: request_id, list: [], error: _})),
                map((resp: { id: number, list: Space[]}) => {
                    this.loading = false;
                    return resp.id === request_id ? resp.list : this.space_list;
                })
            );
            // Process API results
            this.subscription(
                'search_results',
                this.search_results$.subscribe((list) => {
                    this.space_list = list.filter((space) => {
                        if (!space.was_available) {
                            return false;
                        }
                        for (const zone of this.zone_ids) {
                            if (space.zones.includes(zone)) {
                                return true;
                            }
                        }
                        return !this.zone_ids.length;
                    });
                    this.space_list.sort((a, b) => this.sort(a, b));
                })
            );
            this.change$.next('');
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.form) {
            const onChange = (_) => this.change$.next(_);
            /* istanbul ignore else */
            if (this.form.controls.date) {
                this.subscription(
                    'date_field',
                    this.form.controls.date.valueChanges.subscribe(onChange)
                );
            }
            /* istanbul ignore else */
            if (this.form.controls.duration) {
                this.subscription(
                    'duration_field',
                    this.form.controls.duration.valueChanges.subscribe(onChange)
                );
            }
        }
    }

    /**
     * Update the selected space list
     * @param space Space to add/toggle in the selected list
     */
    public selectSpace(space: Space) {
        if (this.multiple) {
            const list = this.spaces.value;
            if (list.find((item) => item.id === space.id)) {
                this.spaces.setValue(list.filter((item) => item.id !== space.id));
            } else {
                this.spaces.setValue(list.concat([space]));
            }
        } else {
            this.spaces.setValue([space]);
            this.event.emit({ type: 'next', step: 'search' });
        }
    }

    /**
     * Proceed to the next step in the space booking flow
     */
    public next(): void {
        this.event.emit({ type: 'next', step: 'search' });
    }

    /** Move flow to previous step */
    public previous(): void {
        this.event.emit({ type: 'previous', step: 'search' });
    }

    /**
     * Compare two spaces to determine order
     * @param space_a
     * @param space_b
     */
    public sort(space_a: Space, space_b: Space) {
        const bld = this._org.buildings.find((bld) => space_a.zones.includes(bld.id));
        const bld_b = this._org.buildings.find((bld) => space_b.zones.includes(bld.id));
        if (bld && bld !== bld_b) {
            return bld.name.localeCompare(bld_b?.name);
        }
        const sort_order = [...bld.sort_order].reverse();
        for (const zone_id of sort_order) {
            if (zone_id === '*') {
                continue;
            }
            const a_has_zone = space_a.zones.indexOf(zone_id) >= 0;
            const b_has_zone = space_b.zones.indexOf(zone_id) >= 0;
            if (a_has_zone && !b_has_zone) {
                return 1;
            } else if (b_has_zone && !a_has_zone) {
                return -1;
            }
        }
        return space_a.name.localeCompare(space_b.name);
    }
}
